"use client";
import { useState } from "react";
import useSWR from "swr";
import Link from "next/link";

const fetcher = (url: string) => fetch(url, { method: "GET" }).then((res) => res.json());

export default function Dashboard() {
  const [page, setPage] = useState(1);
  const limit = 10;
  const [selectedBooking, setSelectedBooking] = useState<any>(null);
  const [modalOpen, setModalOpen] = useState(false);

  const { data, error, mutate } = useSWR(`/api/admin/bookings?page=${page}&limit=${limit}`, fetcher);

  if (error) return <div>Error loading bookings.</div>;
  if (!data) return <div className="p-4">Loading bookings...</div>;

  const { bookings, totalPages, currentPage } = data;

  const handleConfirm = async (bookingId: string) => {
    try {
      const res = await fetch(`/api/admin/bookings/${bookingId}/confirm`, {
        method: "PATCH",
      });
      if (res.ok) {
        mutate();
      } else {
        console.error("Failed to confirm booking");
      }
    } catch (err) {
      console.error(err);
    }
  };

  const handleCancel = async (bookingId: string) => {
    try {
      const res = await fetch(`/api/admin/bookings/${bookingId}/cancel`, {
        method: "PATCH",
      });
      if (res.ok) {
        mutate();
      } else {
        console.error("Failed to cancel booking");
      }
    } catch (err) {
      console.error(err);
    }
  };

  const openModal = (booking: any) => {
    setSelectedBooking(booking);
    setModalOpen(true);
  };

  const closeModal = () => {
    setSelectedBooking(null);
    setModalOpen(false);
  };

  return (
    <div className="min-h-screen p-2 sm:p-4 lg:p-6 bg-gray-100 pt-16 sm:pt-20">
      {/* Header Section */}
      <div className="mb-4 sm:mb-6">
        <h3 className="text-lg sm:text-xl lg:text-2xl font-bold mb-3 sm:mb-4">Dashboard</h3>
        <div className="flex flex-col sm:flex-row gap-2 sm:gap-4">
          <Link href="/admin/dashboard/winery" className="btn btn-primary text-sm sm:text-base w-full sm:w-auto">
            Add Winery
          </Link>
          <Link href="/admin/dashboard/winery/list" className="btn btn-secondary text-sm sm:text-base w-full sm:w-auto">
            Wineries List
          </Link>
        </div>
      </div>

      {/* Bookings Section */}
      <div className="mb-4 sm:mb-6">
        <h1 className="text-base sm:text-lg lg:text-xl font-semibold mb-2 sm:mb-3">Bookings</h1>
        <hr className="border-gray-300" />
      </div>

      {/* Desktop Table View - Hidden on mobile */}
      <div className="hidden lg:block overflow-x-auto">
        <table className="min-w-full border-collapse border border-gray-200 rounded-lg shadow-md bg-white">
          <thead className="bg-gray-200 text-gray-700">
            <tr>
              <th className="w-1/6 border border-gray-200 px-4 py-2 text-left text-sm font-semibold uppercase tracking-wider">
                Booking Id
              </th>
              <th className="w-1/6 border border-gray-200 px-4 py-2 text-left text-sm font-semibold uppercase tracking-wider">
                User Name
              </th>
              <th className="w-1/6 border border-gray-200 px-4 py-2 text-left text-sm font-semibold uppercase tracking-wider">
                Email
              </th>
              <th className="w-1/4 border border-gray-200 px-4 py-2 text-left text-sm font-semibold uppercase tracking-wider">
                Booking Date
              </th>
              <th className="w-1/6 border border-gray-200 px-4 py-2 text-left text-sm font-semibold uppercase tracking-wider">
                Status
              </th>
              <th className="w-1/6 border border-gray-200 px-4 py-2 text-center text-sm font-semibold uppercase tracking-wider">
                Actions
              </th>
            </tr>
          </thead>
          <tbody>
            {bookings && bookings.length > 0 ? (
              bookings.map((booking: any) => (
                <tr
                  key={booking._id}
                  className={`transition-colors duration-200 ${
                    booking._id % 2 === 0 ? "bg-gray-50" : "bg-white"
                  } hover:bg-gray-100`}
                >
                  <td className="w-1/6 border border-gray-200 px-4 py-2 text-gray-800 text-sm font-mono truncate">
                    {booking._id}
                  </td>
                  <td className="w-1/6 border border-gray-200 px-4 py-2 text-gray-800 text-sm truncate">
                    {booking.userId?.name || "N/A"}
                  </td>
                  <td className="w-1/6 border border-gray-200 px-4 py-2 text-gray-800 text-sm truncate">
                    {booking.userId?.email || "N/A"}
                  </td>
                  <td className="w-1/4 border border-gray-200 px-4 py-2 text-gray-800 text-sm truncate">
                    {new Date(booking.createdAt).toLocaleString("en-US", {
                      weekday: "long",
                      year: "numeric",
                      month: "long",
                      day: "numeric",
                      hour: "2-digit",
                      minute: "2-digit",
                      hour12: true,
                    })}
                  </td>
                  <td className="w-1/6 border border-gray-200 px-4 py-2 text-gray-800 text-sm capitalize truncate">
                    {booking.status}
                  </td>
                  <td className="w-1/6 border border-gray-200 px-4 py-2 text-center">
                    <div className="flex justify-center gap-2">
                      {booking.status === "pending" && (
                        <>
                          <button
                            className="bg-green-600 hover:bg-green-700 text-white p-2 rounded-md shadow-sm transition-all duration-200 ease-in-out"
                            onClick={() => handleConfirm(booking._id)}
                            title="Confirm"
                          >
                            <svg
                              className="h-5 w-5"
                              fill="none"
                              stroke="currentColor"
                              viewBox="0 0 24 24"
                              xmlns="http://www.w3.org/2000/svg"
                            >
                              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M5 13l4 4L19 7" />
                            </svg>
                          </button>
                          <button
                            className="bg-red-600 hover:bg-red-700 text-white p-2 rounded-md shadow-sm transition-all duration-200 ease-in-out"
                            onClick={() => handleCancel(booking._id)}
                            title="Cancel"
                          >
                            <svg
                              className="h-5 w-5"
                              fill="none"
                              stroke="currentColor"
                              viewBox="0 0 24 24"
                              xmlns="http://www.w3.org/2000/svg"
                            >
                              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M6 18L18 6M6 6l12 12" />
                            </svg>
                          </button>
                        </>
                      )}
                      <button
                        className="bg-blue-600 hover:bg-blue-700 text-white p-2 rounded-md shadow-sm transition-all duration-200 ease-in-out"
                        onClick={() => openModal(booking)}
                        title="View Details"
                      >
                        <svg
                          className="h-5 w-5"
                          fill="none"
                          stroke="currentColor"
                          viewBox="0 0 24 24"
                          xmlns="http://www.w3.org/2000/svg"
                        >
                          <path
                            strokeLinecap="round"
                            strokeLinejoin="round"
                            strokeWidth="2"
                            d="M15 12a3 3 0 11-6 0 3 3 0 016 0z"
                          />
                          <path
                            strokeLinecap="round"
                            strokeLinejoin="round"
                            strokeWidth="2"
                            d="M2.458 12C3.732 7.943 7.523 5 12 5c4.478 0 8.268 2.943 9.542 7-1.274 4.057-5.064 7-9.542 7-4.477 0-8.268-2.943-9.542-7z"
                          />
                        </svg>
                      </button>
                    </div>
                  </td>
                </tr>
              ))
            ) : (
              <tr>
                <td colSpan={6} className="border border-gray-200 px-4 py-2 text-center text-gray-800">
                  No bookings found.
                </td>
              </tr>
            )}
          </tbody>
        </table>
      </div>

      {/* Tablet Table View - Hidden on mobile and desktop */}
      <div className="hidden md:block lg:hidden overflow-x-auto">
        <table className="min-w-full border-collapse border border-gray-200 rounded-lg shadow-md bg-white">
          <thead className="bg-gray-200 text-gray-700">
            <tr>
              <th className="border border-gray-200 px-3 py-2 text-left text-xs font-semibold uppercase tracking-wider">
                User
              </th>
              <th className="border border-gray-200 px-3 py-2 text-left text-xs font-semibold uppercase tracking-wider">
                Date
              </th>
              <th className="border border-gray-200 px-3 py-2 text-left text-xs font-semibold uppercase tracking-wider">
                Status
              </th>
              <th className="border border-gray-200 px-3 py-2 text-center text-xs font-semibold uppercase tracking-wider">
                Actions
              </th>
            </tr>
          </thead>
          <tbody>
            {bookings && bookings.length > 0 ? (
              bookings.map((booking: any) => (
                <tr
                  key={booking._id}
                  className={`transition-colors duration-200 ${
                    booking._id % 2 === 0 ? "bg-gray-50" : "bg-white"
                  } hover:bg-gray-100`}
                >
                  <td className="border border-gray-200 px-3 py-2 text-gray-800 text-xs">
                    <div>
                      <div className="font-medium truncate">{booking.userId?.name || "N/A"}</div>
                      <div className="text-gray-500 truncate">{booking.userId?.email || "N/A"}</div>
                    </div>
                  </td>
                  <td className="border border-gray-200 px-3 py-2 text-gray-800 text-xs">
                    {new Date(booking.createdAt).toLocaleString("en-US", {
                      month: "short",
                      day: "numeric",
                      hour: "2-digit",
                      minute: "2-digit",
                      hour12: true,
                    })}
                  </td>
                  <td className="border border-gray-200 px-3 py-2 text-gray-800 text-xs capitalize">
                    {booking.status}
                  </td>
                  <td className="border border-gray-200 px-3 py-2 text-center">
                    <div className="flex justify-center gap-1">
                      {booking.status === "pending" && (
                        <>
                          <button
                            className="bg-green-600 hover:bg-green-700 text-white p-1.5 rounded shadow-sm transition-all duration-200 ease-in-out"
                            onClick={() => handleConfirm(booking._id)}
                            title="Confirm"
                          >
                            <svg className="h-4 w-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M5 13l4 4L19 7" />
                            </svg>
                          </button>
                          <button
                            className="bg-red-600 hover:bg-red-700 text-white p-1.5 rounded shadow-sm transition-all duration-200 ease-in-out"
                            onClick={() => handleCancel(booking._id)}
                            title="Cancel"
                          >
                            <svg className="h-4 w-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M6 18L18 6M6 6l12 12" />
                            </svg>
                          </button>
                        </>
                      )}
                      <button
                        className="bg-blue-600 hover:bg-blue-700 text-white p-1.5 rounded shadow-sm transition-all duration-200 ease-in-out"
                        onClick={() => openModal(booking)}
                        title="View Details"
                      >
                        <svg className="h-4 w-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M15 12a3 3 0 11-6 0 3 3 0 016 0z" />
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M2.458 12C3.732 7.943 7.523 5 12 5c4.478 0 8.268 2.943 9.542 7-1.274 4.057-5.064 7-9.542 7-4.477 0-8.268-2.943-9.542-7z" />
                        </svg>
                      </button>
                    </div>
                  </td>
                </tr>
              ))
            ) : (
              <tr>
                <td colSpan={4} className="border border-gray-200 px-4 py-2 text-center text-gray-800">
                  No bookings found.
                </td>
              </tr>
            )}
          </tbody>
        </table>
      </div>

      {/* Mobile Card View - Hidden on tablet and desktop */}
      <div className="md:hidden space-y-3">
        {bookings && bookings.length > 0 ? (
          bookings.map((booking: any) => (
            <div key={booking._id} className="bg-white rounded-lg shadow-md border border-gray-200 p-3">
              <div className="space-y-2">
                <div className="flex justify-between items-start">
                  <div className="flex-1 min-w-0">
                    <p className="text-sm font-medium text-gray-900 truncate">
                      {booking.userId?.name || "N/A"}
                    </p>
                    <p className="text-xs text-gray-500 truncate">
                      {booking.userId?.email || "N/A"}
                    </p>
                  </div>
                  <span className={`px-2 py-1 text-xs font-medium rounded-full capitalize ${
                    booking.status === 'pending' ? 'bg-yellow-100 text-yellow-800' :
                    booking.status === 'confirmed' ? 'bg-green-100 text-green-800' :
                    booking.status === 'cancelled' ? 'bg-red-100 text-red-800' :
                    'bg-gray-100 text-gray-800'
                  }`}>
                    {booking.status}
                  </span>
                </div>
                
                <div className="text-xs text-gray-600">
                  <p className="font-mono text-xs break-all">{booking._id}</p>
                  <p className="mt-1">
                    {new Date(booking.createdAt).toLocaleString("en-US", {
                      month: "short",
                      day: "numeric",
                      year: "numeric",
                      hour: "2-digit",
                      minute: "2-digit",
                      hour12: true,
                    })}
                  </p>
                </div>

                <div className="flex justify-end gap-2 pt-2 border-t border-gray-100">
                  {booking.status === "pending" && (
                    <>
                      <button
                        className="bg-green-600 hover:bg-green-700 text-white px-2 py-1 rounded text-xs font-medium transition-all duration-200 ease-in-out"
                        onClick={() => handleConfirm(booking._id)}
                      >
                        Confirm
                      </button>
                      <button
                        className="bg-red-600 hover:bg-red-700 text-white px-2 py-1 rounded text-xs font-medium transition-all duration-200 ease-in-out"
                        onClick={() => handleCancel(booking._id)}
                      >
                        Cancel
                      </button>
                    </>
                  )}
                  <button
                    className="bg-blue-600 hover:bg-blue-700 text-white px-2 py-1 rounded text-xs font-medium transition-all duration-200 ease-in-out"
                    onClick={() => openModal(booking)}
                  >
                    View
                  </button>
                </div>
              </div>
            </div>
          ))
        ) : (
          <div className="bg-white rounded-lg shadow-md border border-gray-200 p-6 text-center">
            <p className="text-gray-600 text-sm">No bookings found.</p>
          </div>
        )}
      </div>

      {/* Pagination Controls */}
      {totalPages > 1 && (
        <div className="mt-4 sm:mt-6 mb-12 sm:mb-0">
          {/* Mobile Pagination */}
          <div className="md:hidden flex flex-col items-center gap-3">
            <div className="flex items-center gap-2">
              <button 
                className="btn btn-sm disabled:opacity-50 disabled:cursor-not-allowed" 
                disabled={page === 1} 
                onClick={() => setPage((prev) => Math.max(prev - 1, 1))}
              >
                <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M15 19l-7-7 7-7" />
                </svg>
              </button>
              
              <div className="flex items-center gap-1">
                {Array.from({ length: Math.min(3, totalPages) }, (_, i) => {
                  let pageNum;
                  if (totalPages <= 3) {
                    pageNum = i + 1;
                  } else if (currentPage <= 2) {
                    pageNum = i + 1;
                  } else if (currentPage >= totalPages - 1) {
                    pageNum = totalPages - 2 + i;
                  } else {
                    pageNum = currentPage - 1 + i;
                  }
                  
                  return (
                    <button
                      key={pageNum}
                      className={`px-2 py-1 text-xs rounded ${
                        pageNum === currentPage
                          ? 'bg-blue-600 text-white'
                          : 'bg-gray-200 text-gray-700 hover:bg-gray-300'
                      }`}
                      onClick={() => setPage(pageNum)}
                    >
                      {pageNum}
                    </button>
                  );
                })}
              </div>
              
              <button
                className="btn btn-sm disabled:opacity-50 disabled:cursor-not-allowed"
                disabled={page === totalPages}
                onClick={() => setPage((prev) => (prev < totalPages ? prev + 1 : prev))}
              >
                <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M9 5l7 7-7 7" />
                </svg>
              </button>
            </div>
            
            <div className="text-xs text-gray-600">
              Page {currentPage} of {totalPages}
            </div>
          </div>

          {/* Tablet/Desktop Pagination */}
          <div className="hidden md:flex flex-col sm:flex-row justify-center items-center gap-2 sm:gap-4">
            <div className="flex items-center gap-2">
              <button 
                className="btn btn-sm disabled:opacity-50 disabled:cursor-not-allowed" 
                disabled={page === 1} 
                onClick={() => setPage((prev) => Math.max(prev - 1, 1))}
              >
                <svg className="w-4 h-4 mr-1" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M15 19l-7-7 7-7" />
                </svg>
                <span className="hidden sm:inline">Previous</span>
              </button>
              
              <div className="flex items-center gap-1">
                {Array.from({ length: Math.min(5, totalPages) }, (_, i) => {
                  let pageNum;
                  if (totalPages <= 5) {
                    pageNum = i + 1;
                  } else if (currentPage <= 3) {
                    pageNum = i + 1;
                  } else if (currentPage >= totalPages - 2) {
                    pageNum = totalPages - 4 + i;
                  } else {
                    pageNum = currentPage - 2 + i;
                  }
                  
                  return (
                    <button
                      key={pageNum}
                      className={`px-3 py-1 text-sm rounded ${
                        pageNum === currentPage
                          ? 'bg-blue-600 text-white'
                          : 'bg-gray-200 text-gray-700 hover:bg-gray-300'
                      }`}
                      onClick={() => setPage(pageNum)}
                    >
                      {pageNum}
                    </button>
                  );
                })}
              </div>
              
              <button
                className="btn btn-sm disabled:opacity-50 disabled:cursor-not-allowed"
                disabled={page === totalPages}
                onClick={() => setPage((prev) => (prev < totalPages ? prev + 1 : prev))}
              >
                <span className="hidden sm:inline">Next</span>
                <svg className="w-4 h-4 ml-1" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M9 5l7 7-7 7" />
                </svg>
              </button>
            </div>
            
            <div className="text-sm text-gray-600 mt-2 sm:mt-0">
              Page {currentPage} of {totalPages}
            </div>
          </div>
        </div>
      )}

      {/* Booking Details Modal */}
      {modalOpen && selectedBooking && (
        <div className="fixed inset-0 flex items-center justify-center bg-black bg-opacity-50 z-50 p-2 sm:p-4">
          <div className="bg-white p-3 sm:p-4 lg:p-6 rounded-md shadow-md max-w-3xl w-full max-h-[90vh] overflow-y-auto relative">
            <button 
              className="absolute top-2 right-2 text-gray-600 hover:text-gray-800 text-lg sm:text-xl font-bold w-6 h-6 sm:w-8 sm:h-8 flex items-center justify-center rounded-full hover:bg-gray-100" 
              onClick={closeModal}
            >
              ×
            </button>
            <h2 className="text-base sm:text-lg lg:text-xl font-bold mb-3 sm:mb-4 pr-6 sm:pr-8">Booking Details</h2>
            <div className="space-y-3 sm:space-y-4">
              <div className="grid grid-cols-1 sm:grid-cols-2 gap-2 sm:gap-3">
                <div>
                  <strong className="text-xs sm:text-sm text-gray-600">Booking ID:</strong>
                  <p className="text-xs sm:text-sm font-mono break-all">{selectedBooking._id}</p>
                </div>
                <div>
                  <strong className="text-xs sm:text-sm text-gray-600">Status:</strong>
                  <p className="text-xs sm:text-sm capitalize">{selectedBooking.status}</p>
                </div>
                <div>
                  <strong className="text-xs sm:text-sm text-gray-600">User Name:</strong>
                  <p className="text-xs sm:text-sm">{selectedBooking.userId?.name || "N/A"}</p>
                </div>
                <div>
                  <strong className="text-xs sm:text-sm text-gray-600">Email:</strong>
                  <p className="text-xs sm:text-sm break-all">{selectedBooking.userId?.email || "N/A"}</p>
                </div>
              </div>
              
              <div>
                <strong className="text-xs sm:text-sm text-gray-600">Created At:</strong>
                <p className="text-xs sm:text-sm">
                  {new Date(selectedBooking.createdAt).toLocaleString("en-US", {
                    weekday: "long",
                    year: "numeric",
                    month: "long",
                    day: "numeric",
                    hour: "2-digit",
                    minute: "2-digit",
                    hour12: true,
                  })}
                </p>
              </div>

              <div className="mt-3 sm:mt-4">
                <h3 className="text-sm sm:text-base lg:text-lg font-semibold mb-2 sm:mb-3">Wineries:</h3>
                {selectedBooking.wineries && selectedBooking.wineries.length > 0 ? (
                  <div className="space-y-2 sm:space-y-3">
                    {selectedBooking.wineries.map((winery: any, index: number) => (
                      <div key={index} className="border border-gray-200 p-2 sm:p-3 rounded-lg bg-gray-50">
                        <div className="grid grid-cols-1 sm:grid-cols-2 gap-1 sm:gap-2">
                          <div>
                            <strong className="text-xs sm:text-sm text-gray-600">Winery Name:</strong>
                            <p className="text-xs sm:text-sm">{winery.wineryId?.name || "N/A"}</p>
                          </div>
                          <div>
                            <strong className="text-xs sm:text-sm text-gray-600">Date:</strong>
                            <p className="text-xs sm:text-sm">
                              {new Date(winery.datetime).toLocaleString("en-US", {
                                weekday: "long",
                                year: "numeric",
                                month: "long",
                                day: "numeric",
                                hour: "2-digit",
                                minute: "2-digit",
                                hour12: true,
                              })}
                            </p>
                          </div>
                        </div>
                      </div>
                    ))}
                  </div>
                ) : (
                  <p className="text-xs sm:text-sm text-gray-600">No winery details available.</p>
                )}
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
